import React from 'react';
import { Collapse } from 'antd';
import './index.scss'
const { Panel } = Collapse;

export default props => {
    return <>
    <div className="box-fqr">
    <Collapse accordion>
            <Panel header="" key="1" style={{width: 1200}}>
                <p>router-link-exact-active </p>
                <p>router-link-active / /list  router-link  exact     </p>
            </Panel>
            <Panel header="export  import " key="2">
                <p>export </p>
                <p>export  export default </p>
                <p>export  import:</p>
                <p>export 1 </p>
                <p>export 2 </p>
                <p>export default  import:</p>
                <p>export default 1;<br/>
                export default <br/>
                import x from '/xx';</p>
            </Panel>
            <Panel header="async  await " key="3">
                <p>async async  promise</p>
                <p>await  promise await+ promise  promise </p>
                <p>cors  nginx webpack</p>
            </Panel>
            <Panel header="ref " key="4">
                <p> dom </p>
                <p></p>
                <p>this.$refs. </p>
                <p>{'scorllHeight < scrollTop + clientHeiht + 10;'}</p>
            </Panel>
            <Panel header="" key="5">
                <p>route  router </p>
                <p> this.$router.push()</p>
                <p>router-link tag </p>
                <p>(1)  params   /:id /:  (2)  params  path </p>
                <p>//params<br/>
                {`:to="{ name: 'detail', params: { id: item.id } }"`}<br/>
                {`:to=`+"{ {path: `/detail/${item.id}`} }"}</p>
                <p>//query pathname<br/>
                {`:to="{ {path: /detail/, query: { id: item.id }} }"`}</p>
                <p></p>
            </Panel>
            <Panel header="" key="6">
                <p>webpack  vue.config.js</p>
                {`module.exports = {
                  devServer: {
                    proxy: 'http://localhost:3000'
                  }
                };`}
                <p>cors  () // post get put delete Restful  post  put  get  delete </p>
                <p>ngix </p>
                <p>window.name localtion.hash postmessage websocket node  jsonp docment.domain() qq.com vip.qq.com</p>
            </Panel>
            <Panel header="" key="7">
                <p>   vue router  query  params</p>
            </Panel>
        </Collapse>
    </div>
       
    </>
}